import { Component, Element, h, Host, Prop } from '@stencil/core'

@Component({
  tag: 'ato-col',
  shadow: false,
})
export class AtoCol {
  @Prop() size?: 'auto' | string
  @Prop() offset?: string
  @Prop() offsetSm?: string
  @Prop() offsetMd?: string
  @Prop() offsetLg?: string
  @Prop() push?: string
  @Prop() pull?: string

  @Element() private element: HTMLElement

  componentDidLoad() {
    this.element.outerHTML = this.element.outerHTML.replace(/ato/g, 'ion')
  }

  render() {
    return (
      <Host
        size={this.size}
        offset={this.offset}
        offsetSm={this.offset}
        offsetMd={this.offset}
        offsetLg={this.offset}
        push={this.push}
        pull={this.pull}
      >
        <slot />
      </Host>
    )
  }
}
